import React from 'react'
import styled from 'styled-components'
import { Link, useLocation } from 'react-router-dom'
import { ButtonMenu, ButtonMenuItem } from '@pancakeswap/uikit'
import { useTranslation } from 'contexts/Localization'

const StyledNav = styled.nav`
  margin-bottom: 20px;
`

const getActiveIndex = (pathname: string): number => {
  if (
    pathname.includes('/listingform')
  ) {
    return 1
  }
  return 0
}
const Nav = () => {
  const location = useLocation()
  const { t } = useTranslation()
  return (
    <StyledNav>
      <ButtonMenu activeIndex={getActiveIndex(location.pathname)} scale="sm" variant="subtle">
        <ButtonMenuItem id="swap-nav-link" to="/partnership" as={Link}>
          {t('Requirements')}
        </ButtonMenuItem>
        <ButtonMenuItem id="pool-nav-link" to="/listingform" as={Link}>
          {t('Listing Form')}
        </ButtonMenuItem>
      </ButtonMenu>
    </StyledNav>
  )
}

export default Nav
